<template>
  <div>
    <div class="successHead">
      <div class="headLeft">
        <img src="static/success.png">
      </div>
      <div class="Headright">
        <span>预约成功！</span>
        <span>感谢您对我院的支持，请按时就诊，祝您建康</span>
      </div>
    </div>
    <div class="block">
      <block-head :title="'预约信息'"></block-head>
      <div class="verbTime">
        <span>预约时间:</span>
        <span>{{date}}</span>
      </div>
      <p>注: 请提前10分钟内到达门店等候服务</p>
      <dotted-line></dotted-line>
      <info-row :left="'预约服务'" :right="'朱氏头皮针'"></info-row>
      <info-row :left="'服务价格'" :right="'200'"></info-row>
      <p>门店信息</p>
      <div class="shopInfoBox">
        <div>
          <span>就诊时间:</span>
        </div>
        <div>
          <span>上午 9:00-11:30</span>
          <span>下午 13:00-15:30</span>
        </div>
      </div>
      <div class="shopInfoBox">
        <div>
          <span>地&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;址:</span>
        </div>
        <div class="local">
          <span>上海市浦东新区即墨路150号东方医院特需门诊三楼</span>
        </div>
        <div class="guide">
            <span>导航</span>
        </div>
      </div>
      <dotted-line></dotted-line>
      <info-row :left="'会员'" :right="name" class="vipInfo"></info-row>
      <info-row :left="'性别'" :right="sex" class="vipInfo"></info-row>
      <info-row :left="'年龄'" :right="age" class="vipInfo"></info-row>
      <info-row :left="'手机号码'" :right="telPhone" class="vipInfo"></info-row>
      <info-row :left="'提交时间'" :right=" '' | formatDate2" class="vipInfo"></info-row>
    </div>
    <div class="verbBox">
        <div class="verbBtn" @click="goCancelVerb">
            <span>取消预约</span>
        </div>
    </div>
  </div>
</template>

<script>

import blockHead from 'components/blockHead/blockHead'
import dottedLine from 'components/dottedLine/dottedLine'
import infoRow from 'components/infoRow/infoRow'
import { formatDate, cancleSubscription} from '@/api'
export default {
  mounted () {
      this.date = this.$route.query.date
      this.name = this.$route.query.name
      this.age = this.$route.query.age
      this.sex = this.$route.query.sex
      this.telPhone = this.$route.query.telPhone
      this.subId = this.$route.query.subId
  },
  filters: {
    formatDate2 () {
      var date = new Date()
      return formatDate(date, 'yyyy-MM-dd hh:mm:ss')
    }
  },
  data () {
    return {
        date: '',
        name: '',
        age: '',
        sex: '',
        telPhone: '',
        subId: ''
    }
  },
  components: {
    blockHead,
    dottedLine,
    infoRow
  },
  methods: {
    goCancelVerb: function () {
      var _this2 = this
      var params = new URLSearchParams()
      params.append('subId', this.subId)
      cancleSubscription(params).then(function (data) {
        if (!data.success) {
            alert(data.error)
        }else{
            _this2.gotoCancle()
        }
      }).catch(function (data) {
        alert('系统异常，请稍后再试')
      })
    },
    gotoCancle: function () {
     var _this3 = this
     this.$router.push({
        path: '/cancelVerb',
         query: {date: _this3.date,name:_this3.name,age:_this3.age,telPhone:_this3.telPhone,sex:_this3.sex}
      })
     }
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus" scoped>
@import "~common/stylus/variable"
@import "~common/stylus/base"

.successHead
    background-color white
    @extend .flex
    @extend .flex-row
    @extend .flex-start
    .headLeft
        width 80px
        height 80px
        @extend .flex
        @extend .flex-around
        
    img
        width 50px
    .Headright
        @extend .flex
        @extend .flex-column
        justify-content space-around
        text-align left
        span
            line-height 20px
        span:first-of-type
            color black
        span:last-of-type
            color $color-theme-gray_1
            font-size $font-size-level_0

.block
    background-color white
    margin 10px 10px 0px 10px
    padding-bottom 20px
    p:first-of-type
        color $color-theme-gray_1
        font-size $font-size-level_0
        text-align right
        padding-right 10px
    p:last-of-type
        text-align left
        margin-top 30px 
        padding-left 16px
        font-size $font-size-level_3
        font-weight $font-weight-level_5
    .verbTime
        @extend .flex
        @extend .flex-row
        @extend .flex-start
        span
            line-height 60px
            font-weight $font-weight-level_7
        span:first-of-type
            width 100px
        span:last-of-type
            flex-grow 2
            text-align right
            padding-right 10px
    .vipInfo
        margin-top 10px

.verbBox
    @extend .flex
    @extend .flex-row
    @extend .flex-around
    margin-top 20px
    padding-bottom 40px

.verbBtn
    width 70%
    height 40px
    border 1px solid $color-theme-blue
    background-color white
    span
        line-height 40px
        color $color-theme-blue
        font-weight $font-weight-level_7

.shopInfoBox
    @extend .flex
    @extend .flex-row
    @extend .flex-start
    align-items flex-start
    margin-top 14px
    font-size $font-size-level_2
    div:first-of-type
        width 100px
        text-align left
        padding-left 16px
    div:nth-of-type(2)
        @extend .flex
        @extend .flex-column
        flex-grow 2
        text-align left
        color $color-theme-gray_1
        span:not(:first-of-type)
            margin-top 10px
    .local
        margin 0px 10px 0px 25px
        border-right 1px solid $color-theme-gray_1
        span
            color black
    .guide
        color $color-theme-blue
        width 100px
        margin-top 8px

</style>


